import ee from 'event-emitter'
import store from '../store'
import router from '../router'
//import {encodeMsgBody,decodeMsgBody,encodeLoginReq} from '../proto/score'
import messages from '../proto/score_pb.js'
let emitter = ee();
let ws;

function onOpen () {
  emitter.emit("open");
  console.log("onopen");
};

function onMessage (e) {
  // e.data = []
  //   console.log(e.data);
  //   // if(typeof e.data ==='object'){
  //   //   e.data = new ArrayBuffer(8)
  //   // }
  //   let arrayBuffer = e.data
  // let array = Array.prototype.slice.call(new Uint8Array(arrayBuffer ));
  // console.log(array,111);
  // var arr = [8, 1, 18, 2, 8, 0]
  // var data = new Uint8Array(arr).buffer;
  // console.log(data,12);


  //   console.log(typeof e.data ==='Object',e,2121);

  let message = messages.MsgBody.deserializeBinary(e.data);
  message = message.toObject()
  console.log("onmessage", message);

  switch (message.id) {
    case messages.CmdID.LOGIN_RSP:
      console.log(message.data);

      let loginRsp = new messages.LoginRsp.deserializeBinary(message.data);
      loginRsp = loginRsp.toObject();

      store.commit("updateMessage", "登录响应" + loginRsp.code);

      if (loginRsp.code == 0) {
        store.commit("updateLogin", true);
      }

      console.log("loginRsp", loginRsp);
      break;

    case messages.CmdID.BEGIN_SCORE_RSP:
      let beginScoreRsp = new messages.BeginScoreRsp.deserializeBinary(message.data);
      beginScoreRsp = beginScoreRsp.toObject();

      store.commit("updateMessage", "开始打分响应" + beginScoreRsp.code);
      console.log('====================================');
      console.log(beginScoreRsp);
      console.log('====================================');
      emitter.emit(message.id, beginScoreRsp);
      break;

    case messages.CmdID.SCORE_RESULT:

      // CAASqgJ7ImF1ZGlvVXJsIjoiLm1wMyIsImR0TGFzdFJlc3Bvbn
      console.log(message.data);

      let scoreResult = new messages.ScoreResult.deserializeBinary(message.data);
      scoreResult = scoreResult.toObject();

      store.commit("updateMessage", "打分结果" + scoreResult.code);

      if (scoreResult.code == 0) {
        let result = JSON.parse(scoreResult.result);
        store.commit("updateResult", result);
      }
      scoreResult.result = JSON.parse(scoreResult.result)
      console.log("scoreResult", scoreResult);
      break;

  }

  //let data = decodeMsgBody(e.data);
  //console.log("onmessage",data);
};

function onClose () {
  console.log("onclose");
  store.commit("updateLogin", false);
};

function init () {
  if (ws) {
    ws.close();
  }

  //ws = {send:function(){}}
  ws = new WebSocket("wss://score.51talk.com");
  ws.binaryType = "arraybuffer";
  ws.addEventListener("open", onOpen);
  ws.addEventListener("message", onMessage);
  ws.addEventListener("close", onClose);
}

function MsgBody (id, data) {
  let msgBody = new messages.MsgBody();
  msgBody.setId(id);
  data && msgBody.setData(data);
  return msgBody.serializeBinary();
}


export function login (name, password) {
  init();

  emitter.once("open", function () {
    let loginReq = new messages.LoginReq();
    loginReq.setName(name);
    loginReq.setPassword(password);

    let msgBody = MsgBody(messages.CmdID.LOGIN_REQ, loginReq.serializeBinary());
    ws.send(msgBody);
  })
}

export async function begin (text) {
  let CoreType = "word.eval"
  if (text.indexOf(" ") != -1) {
    CoreType = "sent.eval";
  }
  let o = {
    RefText: text,
    AudioType: store.state.audioType,
    CoreType: CoreType,
    Custom: {}
  }
  console.log("begin", o);
  let beginScoreReq = new messages.BeginScoreReq();
  beginScoreReq.setUserjson(JSON.stringify(o));

  let msgBody = MsgBody(messages.CmdID.BEGIN_SCORE_REQ, beginScoreReq.serializeBinary());
  ws.send(msgBody);

  return new Promise((resolve, reject) => {
    emitter.once(messages.CmdID.BEGIN_SCORE_RSP, function (data) {
      console.log("beginScoreRsp", data);
      if (data.code == 0) {
        resolve(data);
      } else {
        reject(data);
      }
    })
  })
}


export function end () {
  let msgBody = MsgBody(messages.CmdID.END_SCORE);
  ws.send(msgBody);
}

export function audio (byte) {
  // console.log(byte);
  let audioData = new messages.AudioData();
  audioData.setData(byte);

  let msgBody = MsgBody(messages.CmdID.AUDIO_DATA, audioData.serializeBinary());
  ws.send(msgBody);
}