//import Recorder from 'opus-recorder'
import Recorder from '../../recorder/recorder'
import {begin,end,audio} from '../../util/connect'
import store from '../../store'

let rec;
let audioBuffers = [];


export function initRecord({state, commit}){
    console.log("initRecord",state.audioType)
    if(state.audioType == "pcm"){
        rec = new Recorder({
            monitorGain: 0,
            recordingGain: 1,
            numberOfChannels: 1,
            wavBitDepth: 16,
            streamPages: true,
            bufferLength: 2048,
            wavSampleRate: 16000,
            // encoderPath: "./js/encoderWorker.js"
            encoderPath: "./js/waveWorker.js",
            //reuseWorker:true
        })
    }else if(state.audioType == "opus"){
        rec = new Recorder({
            monitorGain: 0,
            recordingGain: 1,
            numberOfChannels: 1,
            encoderApplication: 2048,
            encoderFrameSize: 60,
            encoderSampleRate: 16000,
            maxFramesPerPage: 40,
            streamPages: true,
            originalSampleRateOverride: 16000,
            encoderPath: "./js/encoderWorker.min.js"
        })
    }

    rec.onstart = function(){
        console.log('Recorder is started11111');
        store.commit("updateRecodring",true);
    };

    rec.onstop = function(e){
        console.log('Recorder is stopped');
        // console.log('====================================');
        // console.log(state);
        // console.log(e,rec,audioBuffers);
        // console.log('====================================');
        store.commit("updateRecodring",false);
    };


    rec.ondataavailable = function(typedArray){
        audio(typedArray);
        
        // console.log(typedArray,212121);
        //边录边存
        audioBuffers.push( typedArray );
    }
}


function wavFilerequestData(){
    var bufferLength = audioBuffers[0].length;
    var dataLength = audioBuffers.length * bufferLength;
    var headerLength = 44;
    var wav = new Uint8Array( headerLength + dataLength );
    var view = new DataView( wav.buffer );
    var numberOfChannels = 1;
    var sampleRate = 16000;
    var bytesPerSample = 2;
    var bitDepth = 16;

    view.setUint32( 0, 1380533830, false ); // RIFF identifier 'RIFF'
    view.setUint32( 4, 36 + dataLength, true ); // file length minus RIFF identifier length and file description length
    view.setUint32( 8, 1463899717, false ); // RIFF type 'WAVE'
    view.setUint32( 12, 1718449184, false ); // format chunk identifier 'fmt '
    view.setUint32( 16, 16, true ); // format chunk length
    view.setUint16( 20, 1, true ); // sample format (raw)
    view.setUint16( 22, numberOfChannels, true ); // channel count
    view.setUint32( 24, sampleRate, true ); // sample rate
    view.setUint32( 28, sampleRate * bytesPerSample * numberOfChannels, true ); // byte rate (sample rate * block align)
    view.setUint16( 32, bytesPerSample * numberOfChannels, true ); // block align (channel count * bytes per sample)
    view.setUint16( 34, bitDepth, true ); // bits per sample
    view.setUint32( 36, 1684108385, false); // data chunk identifier 'data'
    view.setUint32( 40, dataLength, true ); // data chunk length

    for (var i = 0; i < audioBuffers.length; i++ ) {
        wav.set( audioBuffers[i], i * bufferLength + headerLength );
    }

    var dataBlob = new Blob( [wav.buffer], { type: 'audio/wav' } );
    var fileName = new Date().toISOString() + ".wav";
    var url = URL.createObjectURL( dataBlob );

    var audio = document.createElement('audio');
    audio.controls = true;
    audio.src = url;

    var link = document.createElement('a');
    link.href = url;
    link.download = fileName;
    link.innerHTML = link.download;

    var li = document.createElement('li');
    li.appendChild(link);
    li.appendChild(audio);


    var selectedFile = document.getElementById('recordingslist');
    selectedFile.appendChild(li);
}

function opusFilerequestData() {
    var bufferLength = audioBuffers[0].length;
    var dataLength = audioBuffers.length * bufferLength;
    var headerLength = 44;
    var wav = new Uint8Array(headerLength + dataLength);
    var view = new DataView(wav.buffer);
    var numberOfChannels = 1;
    var sampleRate = 16000;
    var bytesPerSample = 2;
    var bitDepth = 16;
}

/**
 * 开始录音
 */
export async function startRecord({state, commit}) {
    await begin(state.word);

    rec.start().catch(function(e){
        commit("updateMessage",'Error encountered:', e.message);
    });
}


/**
 * 停止录音
 */
export function stopRecord({state, commit}) {
    rec.stop();
    // wavFilerequestData();
    setTimeout(end,1000);//防止结束后多发语音包
    //end();
}
