<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>

    export default {
        data() {
            return {}
        },

        watch: {
            "$store.state.message": function (message) {
                if(!message)return;

                let _this = this;
                this.$message({
                    message:message,
                    onClose:function(){
                        _this.$store.commit("updateMessage","")
                    }
                });
            },
        },

        mounted: function () {

        }
    }

</script>

