// Action 提交的是 mutation，而不是直接变更状态。
// Action 可以包含任意异步操作。
import {login as connectLogin} from '../../util/connect'
import router from '../../router'
/* 导出actions方法 */
import {initRecord,startRecord,stopRecord} from './record2'

export default {
    initRecord,
    startRecord,
    stopRecord,
    changeWord,
    login
}


function login({state, commit},{name,password}){
    connectLogin(name,password);
}

function changeWord({state, commit}){
    let random = Math.floor(Math.random()*state.test.length);
    let word = state.test[random];
    commit("updateWord",word);
}